@import "./normalize.scss";

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

button {
  border: none;
  background-color: inherit;
  cursor: pointer;
}

@import "./abstracts/variables";
@import "./abstracts/mixins";

@import "./header.scss";
