@import "./components/hamburger";

@import "./main/main";
@import "./components/swiper";
@import "./components/buttons/btn-orange";
@import "./components/buttons/btn-default";
@import "./components/buttons/btn-outline";
@import "./components/buttons/btn-material";
@import "./components/buttons/btn-filter";

@import "./catalog";
@import "./interior-doors";
@import "./options";
@import "./components/card";

@import "./components/filters/filter-prices";

@import "./furniture";
@import "./publicity";
@import "./gallery";
