@import "./components/socials";

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

body {
  font-family: "Inter";
}

.container {
  max-width: 1176px;
  margin: 0 auto;
  width: 90%;
  padding: 0 20px;

  @media (max-width: 800px) {
    width: auto;
  }
}

.swiper-wrapper {
  // height: 400px;
}

.section__title {
  @include text(32px, 600, 32px, $secondaryColor);

  @media (max-width: 800px) {
    font-size: 18px;
    line-height: 28px;
  }
}
